
import { defineComponent, onMounted, ref } from "vue";
import { TreeInstance, TreeNodeOptions } from "../components";
import { getCheckNodes, getHalfCheckNodes } from "./uses";

function recursion(path = "0", level = 3): TreeNodeOptions[] {
  const list = [];
  for (let i = 0; i < 10; i++) {
    const nodeKey = `${path}-${i}`;
    const treeNode: TreeNodeOptions = {
      nodeKey,
      name: nodeKey,
      children: [],
      hasChildren: true,
    };

    if (level > 0) {
      treeNode.children = recursion(nodeKey, level - 1);
    } else {
      treeNode.hasChildren = false;
    }

    list.push(treeNode);
  }
  return list;
}

export default defineComponent({
  name: "CheckboxDemo",
  setup() {
    const list = ref<TreeNodeOptions[]>([]);
    const virTreeOne = ref<TreeInstance | null>(null);
    const virTreeTwo = ref<TreeInstance | null>(null);
    const defaultExpandedKeys = ref(["0-2"]);
    const defaultCheckedKeys = ref(["0-0-0", "0-2"]);

    onMounted(() => {
      list.value = recursion();
    });
    const halfNodes = () => {
      getHalfCheckNodes(virTreeOne.value!);
    };
    const checkedNodes = () => {
      getCheckNodes(virTreeTwo.value!);
    };
    return {
      list,
      virTreeOne,
      virTreeTwo,
      halfNodes,
      checkedNodes,
      defaultExpandedKeys,
      defaultCheckedKeys,
    };
  },
});
