
import { defineComponent, onMounted, ref } from "vue";
import { TreeInstance, TreeNodeOptions } from "../components";
import { getCheckNodes } from "./uses";

function recursion(path = "0"): TreeNodeOptions[] {
  const list = [];
  for (let i = 0; i < 2; i += 1) {
    const nodeKey = `${path}-${i}`;
    const treeNode: TreeNodeOptions = {
      nodeKey,
      name: nodeKey,
      children: [],
      hasChildren: true,
    };
    list.push(treeNode);
  }
  return list;
}

export default defineComponent({
  name: "AsyncDataDemo",
  setup(prop, { emit }) {
    const list = ref<TreeNodeOptions[]>([]);
    const virTree = ref<TreeInstance | null>(null);
    const defaultCheckedKeys = ref([]);
    onMounted(() => {
      list.value = recursion();
    });
    const lazyLoad = (
      node: TreeNodeOptions,
      callback: (children: TreeNodeOptions[]) => void
    ) => {
      console.log("lazyLoad", node);
      const result: TreeNodeOptions[] = [];
      for (let i = 0; i < 2; i += 1) {
        const nodeKey = `${node.nodeKey}-${i}`;
        const treeNode: TreeNodeOptions = {
          nodeKey,
          name: nodeKey,
          children: [],
          hasChildren: true,
        };
        result.push(treeNode);
      }
      setTimeout(() => {
        callback(result);
      }, 500);
    };
    const checkedNodes = () => {
      getCheckNodes(virTree.value!);
    };
    return {
      list,
      virTree,
      lazyLoad,
      defaultCheckedKeys,
      checkedNodes,
    };
  },
});
